.container.testimonials_container {
    width: 40%;
    padding-bottom: 4rem;

}

.client_avatar {
    width: 10rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 5rem;
    border: 0.1rem solid var(--color-primary-variant);
}

.testimonial {
    background: var(--color-primary-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client_review {
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
    background: var(--color-primary);
}


/* Media Queries - Tablets/Ipads display (Medium Devices)*/

@media screen and (max-width: 1024px) {
    .container.testimonials_container {
        width: 60%;
    }
}
     
     
     /* Media Queries - Iphone/Androids display (Small Devices)*/
     
     @media screen and (max-width: 600px) {
        .container.testimonials_container {
            width: var(--container-width-sm);
        }

        .client_review {
            width: var(--container-width-sm);
        }
     }