header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;

}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA (call to action)*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* Header Socials*/

.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 20rem;
    bottom: 8rem;
    color: var(--color-primary);
}

.header_socials a {
    color: var(--color-primary);
}

.header_socials::after {
    content: '';
    width: 1px;
    height: 5rem;
    background: var(--color-primary);

}

/* ME */
.headShot {
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* Scroll Down */

.scroll__down {
    position: absolute;
    right: 16rem;
    bottom: 15rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.5rem;
    color: var(--color-primary);
}

/* Media Queries (Medium Devices)*/

@media screen and (max-width: 1024px) {
header {
    height: 68vh;
}
}


/* Media Queries (Small Devices)*/

@media screen and (max-width: 1024px) {
header {
    height: 100vh;
}
.header__socials,
.scroll__down {
    display: none
}
}